* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

h1 {
  margin: 0; }

body {
  font-family: 'Raleway', sans-serif;
  font-weight: 300; }

.section {
  width: 100%; }
  .section--about {
    background-color: #00bbbb; }
  .section--services {
    background-color: #70bfff; }
  .section--gallery {
    background-color: #ffe92f; }
  .section--subscription {
    background-image: url("../img/subscribe_bcg.jpg");
    background-position: right;
    background-repeat: no-repeat; }
  .section--blog {
    background-color: #b07cf7; }
  .section--contact {
    background: url("../img/footer_bcg.jpg") no-repeat;
    background-position: center;
    background-size: cover; }
  .section--footer {
    background-color: #262626; }

.section__content {
  width: 80%;
  margin: 0 auto; }

header {
  width: 80%;
  position: fixed;
  display: flex;
  margin: 40px 10%;
  justify-content: space-between; }

.navigation__items {
  vertical-align: middle;
  display: flex;
  font-weight: 700; }

.navigation__item {
  vertical-align: middle;
  list-style: none;
  text-transform: uppercase;
  padding: 20px;
  font-size: 16px; }

.navigation__item:last-child {
  padding-right: 0; }

.hero {
  height: 100vh;
  width: 80%;
  margin: 0 auto; }
  .hero__background {
    position: absolute;
    background-image: url(../img/header_bcg.jpg);
    height: 100vh;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
    width: 100%;
    background-size: cover;
    background-position: center;
    z-index: -1; }
  .hero__content {
    padding-top: 20%; }
    .hero__content h1 {
      font-weight: 900;
      font-size: 60px; }
    .hero__content p {
      width: 25%;
      margin-top: 10px;
      line-height: 1.5; }
    .hero__content__btn {
      text-transform: uppercase;
      padding: 10px;
      font-size: 20px;
      margin-top: 20px;
      color: white;
      background: #ff6033;
      cursor: pointer;
      border: none;
      box-shadow: 0px 4px 5px 1px #d1c2d1;
      transition: transform .3s ease-in-out; }
    .hero__content__btn:hover {
      background: #fa5c13;
      transform: scale(1.1); }

.about {
  display: flex;
  justify-content: space-between;
  color: white; }
  .about__image {
    width: 50%; }
    .about__image img {
      width: 100%; }
  .about__informations {
    margin: auto 0;
    width: 50%;
    margin-left: 5%; }
  .about h1 {
    font-size: 90px; }
  .about p {
    padding-bottom: 15px;
    line-height: 1.5;
    width: 65%; }

.services {
  display: flex;
  justify-content: space-between;
  color: white; }
  .services__list {
    align-self: center;
    margin-left: 10%; }
  .services__image {
    position: relative;
    width: 50%; }
    .services__image img {
      width: 100%;
      padding: 10%; }
    .services__image .image__title {
      font-size: 90px;
      width: 100%;
      text-align: center;
      font-weight: 700;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }

.service {
  color: black;
  font-weight: 700;
  background-color: white;
  font-size: 20px;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  box-shadow: 3px 2px 9px 0px rgba(0, 0, 0, 0.75); }
  .service p {
    margin: auto;
    width: 80%;
    text-align: center; }
  .service span {
    width: 20%;
    height: 100%;
    border-left: 1px solid #9d9d9d;
    text-align: center; }

.gallery {
  padding-top: 90px;
  padding-bottom: 90px;
  text-align: center; }
  .gallery__title {
    font-size: 90px; }
  .gallery__decription {
    font-size: 17px;
    width: 40%;
    margin: 30px auto; }
  .gallery__presentation {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: minmax(100px, auto);
    margin: 0 auto;
    grid-gap: 30px;
    justify-items: stretch;
    align-items: stretch;
    grid-template-areas: "peach peach peach peach blackberries blackberries" "peach peach peach peach blueberries blueberries" "milk milk milk khakki khakki khakki"; }
  .gallery .image-1 {
    grid-area: peach; }
  .gallery .image-2 {
    grid-area: blackberries; }
  .gallery .image-3 {
    grid-area: blueberries; }
  .gallery .image-4 {
    grid-area: milk; }
  .gallery .image-5 {
    grid-area: khakki; }
  .gallery__btn {
    padding: 10px 25px;
    margin-top: 30px;
    font-size: 20px;
    color: white;
    background: #5585a2;
    cursor: pointer; }

.subscription {
  min-height: 500px;
  padding-top: 100px; }
  .subscription__title {
    font-size: 40px; }
  .subscription__description {
    font-size: 17px;
    width: 45%;
    margin: 25px 0; }
  .subscription__form input {
    width: 50%;
    padding: 10px;
    margin-right: 20px; }
  .subscription__form button {
    padding: 10px 25px;
    color: white;
    background-color: #96b92c;
    cursor: pointer; }

.blog {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center; }
  .blog__title {
    font-size: 90px;
    color: white;
    margin-bottom: 30px; }
  .blog__description {
    width: 55%;
    margin: 0 auto 30px;
    color: white;
    font-weight: 400; }
  .blog__btn {
    padding: 10px 30px;
    color: white;
    background-color: #f72b3b;
    cursor: pointer;
    font-weight: 300;
    border: none;
    box-shadow: 2px 5px 12px -6px rgba(0, 0, 0, 0.75); }

.blog__posts {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px; }
  .blog__posts .post {
    margin: 10px;
    position: relative;
    width: 33%; }
    .blog__posts .post__image {
      width: 100%;
      height: 100%;
      position: relative; }
    .blog__posts .post__date {
      position: absolute;
      top: 5%;
      left: 5%;
      padding: 2%;
      background-color: rgba(0, 0, 0, 0.4);
      border-radius: 10%;
      color: white;
      font-weight: 700;
      font-size: 21px; }
    .blog__posts .post__summary {
      width: 100%;
      position: absolute;
      height: 30%;
      bottom: 0;
      background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
      text-align: left; }
      .blog__posts .post__summary__title {
        font-size: 20px;
        color: white;
        text-shadow: black 2px 2px 2px;
        margin-top: 10px;
        margin-left: 20px;
        padding-bottom: 20px; }
      .blog__posts .post__summary__author {
        font-size: 13px;
        color: white;
        text-shadow: black 2px 2px 2px;
        margin-left: 20px; }

.contact {
  padding-bottom: 100px; }
  .contact .flex-container {
    display: flex; }
  .contact__heading {
    text-align: center; }
  .contact__title {
    font-size: 90px;
    padding-top: 100px; }
  .contact__description {
    padding: 50px 0; }
  .contact__form {
    display: flex;
    flex-direction: column;
    width: 50%; }
    .contact__form input, .contact__form textarea {
      width: 85%;
      font-size: 15px;
      padding: 15px 20px;
      margin-bottom: 15px;
      background: rgba(0, 196, 196, 0.3);
      border: 0;
      color: white;
      outline: none;
      resize: none; }
    .contact__form input:focus, .contact__form textarea:focus {
      background: #00c4c4;
      border-left: 4px solid #355b82; }
    .contact__form input::placeholder, .contact__form textarea::placeholder {
      color: white;
      opacity: 0.8; }
    .contact__form textarea {
      height: 300px; }
  .contact__address {
    text-align: left;
    padding-left: 10%; }
    .contact__address h4 {
      font-size: 17px; }
    .contact__address p {
      font-size: 20px;
      padding-bottom: 15px; }

.footer {
  color: #b9b9b9;
  display: flex;
  padding: 30px 0;
  justify-content: space-between; }
  .footer span {
    color: red;
    font-weight: 700; }
